@import '../../theme.scss';
.mobile {
    width: 100vw;
    height: 64px;
    .description{
        font-size: 2em;
        text-align: center;
        width: 128px;
        transform: translateX(-25%);
    }
}
.way {
    position: absolute;
    top: calc(50% + 58px);
    transform: translateY(-50%);
    border: 3px dashed $primaryColorLight;
    height: 0px;
    width: 68vw;
    left: 15vw;
}
